import React, { Component } from 'react';
import TodoItem from 'components/TodoItem';

import style from './style.scss'

export default class TodoList extends Component {
    
    render () {
        const { todos } = this.props;
        return (
            <div className={style.container}>
                <div className={style.filters}>
                    <div>
                        <label className={style.label}>
                            <input type="checkbox" name="SHOW_ALL" />
                            Show All
                        </label>
                    </div>
                    <div>
                        <label className={style.label}>
                            <input type="checkbox" name="SHOW_COMPLETED" />
                            Show Completed
                        </label>
                    </div>
                    <div>
                        <label className={style.label}>
                            <input type="checkbox" name="INSHOW_COMPLETED" />
                            Show Incompleted
                        </label>
                    </div>
                </div>
                <input className={style.enterField} type="text" placeholder="Enter the task" />
                <ul className={style.list}>
                    {todos.map(todo => {
                        return <TodoItem  text={todo.text} completed={todo.completed} id={todo.id}/>
                    })}
                </ul>
            </div>
        )
    }
}